import Draw_tablet from './video/Draw_tablet.mp4'
import { faEnvelope,  faPhone } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';

export default function HomeScreen() {
const VideoSrc = Draw_tablet

return (
    <body>

    <header id="header" class="header">
        <div class="header-content">
            <div class="container">
                <div class="row">
                    <div class="middlediv">
                        <h1 class="kliffsstorelarge">Kliff's Store</h1>
                        <a class="bn60" href="#shop">Shop Now</a>
                    </div> 
                </div> 
            </div> 
        </div>        
        <video 
        autoPlay 
        loop
        muted
        id="video-background" 
        poster="img/draw_tablet.PNG" 
        playsinline
        >
            <source src={VideoSrc} type="video/mp4" />
        </video>
    </header> 



    {/* <Contact  */}
    <div id="contact" class="form-1">
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <h2 class="contactH-heading">Contact details</h2>
                    <p class="contactP-heading">Fill out information bellow to Contact Us</p>
                    <ul class="list-unstyled li-space-lg">
                        <li> <FontAwesomeIcon icon= {faMapMarkerAlt}></FontAwesomeIcon> West Palm Beach FL, US</li>
                        <li> <FontAwesomeIcon icon= {faPhone}></FontAwesomeIcon> <a href="tel:00817202212">+1 777-777-7777</a></li>
                        <li> <FontAwesomeIcon icon= {faEnvelope}></FontAwesomeIcon> <a href="mailto:contact@site.com">kliffsStore@gmail.com</a></li>
                    </ul>
                </div> 
            </div> 
            <div class="row">
                <div>
                    
                    {/* <Contact Form  */}
                    <form>
                        <div class="form-group">
                            <input type="text" class="form-control-input " placeholder="Name" required></input>
                        </div>
                        <div class="form-group">
                            <input type="email" class="form-control-input" placeholder="Email" required></input>
                        </div>
                        <div class="form-group">
                            <textarea class="form-control-textarea" placeholder="Message" required></textarea>
                        </div>
                        <div class="form-group">
                            <button type="submit" class="form-control-submit-button">Submit</button>
                        </div>
                    </form>

                </div> 
            </div> 
        </div> 
    </div> 
    </body>
)
}